* {
  box-sizing: border-box;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

ul,
ol {
  margin: 0;
  padding-left: 0;
}

li {
  list-style: none;
}

img {
  display: block;
  width: 100%;
  height: auto;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  /* -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; */
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
  padding: 20px;
}

.wrapper {
  height: 60px;
  background-color: aqua;
}

.search-form {
  position: relative;
  width: 300px;
  margin: 0 auto;
  padding: 10px;
}

input,
.btn-search-form {
  outline: none;
  background: transparent;
}

input {
  width: 100%;
  height: 42px;
  padding-left: 15px;
  border: 3px solid #f3a109;
  /* color: black; */
}

input:focus,
input:hover {
  border-color: #38e90c;
}

.btn-search-form {
  border: none;
  height: 42px;
  width: 42px;
  position: absolute;
  top: 50%;
  transform: translate(0, -50%);
  right: 8px;
  cursor: pointer;
  color: #f3a109;
}

.btn-search-form:before {
  content: '\f002';
  font-family: FontAwesome;
  font-size: 16px;
  color: currentColor;
}

.btn-search-form:focus,
.btn-search-form:hover {
  color: #38e90c;
}

.gallery {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 8px;
  margin-bottom: 10px;
}
